<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ManageServiceCrew}}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <!-- <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditFSMCrew')"></div>
                                </div>
                            </div>
                        </span> -->
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        CrewResId: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            ManageServiceCrew: this.$t('AddFsmCrewResource'),
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ResourceName'),
                            name: "ResourceName",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectResource'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('StartDateTime'),
                            name: "StartDate",
                            value: '',
                            placeholder: this.$t('StartDateTime'),
                            config: { format: "M/D/YYYY" }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDateTime'),
                            name: "EndDate",
                            value: '',
                            placeholder: this.$t('EndDateTime'),
                            config: { format: "M/D/YYYY" }
                        },
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "toggleField",
                            label: this.$t('IsLeader'),
                            name: "IsLeader",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
        this.GetResourceDdlRecords();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            vueObj.ManageServiceCrew="";
            if(vueObj.CrewResId.CREW_RES_ID >0){
                vueObj.ManageServiceCrew=this.$t('EditFsmCrewResource');
            }else{
                vueObj.ManageServiceCrew=this.$t('AddFsmCrewResource');
            }
            var url = `Id=${vueObj.CrewResId.CREW_RES_ID}`;
            DataServices.FsmGetCrewResourceByCrewId(url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.result.data[0].resource_id;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.result.data[0].status_id;
                            (vueObj.FormSchema)[1].Data[0].value = response.data.result.data[0].startDt;
                            (vueObj.FormSchema)[1].Data[1].value = response.data.result.data[0].endDt;
                            (vueObj.FormSchema)[2].Data[0].value = response.data.result.data[0].isleader;
                        }
                    }
                }
            );
        },
        Close: function () {
            this.$parent.AddEditCrewResourcePopup = false;
        },
        GetResourceDdlRecords: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_RESOURCENAME'}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                Id: vm.CrewResId.CREW_RES_ID == null ? 0 : vm.CrewResId.CREW_RES_ID,
                CrewID: this.$route.params.id,
                ResourceId: formfields.ResourceName,
                IsLeader: formfields.IsLeader,
                StartDt: formfields.StartDate == "" ? null : vm.$options.filters.formatDate(formfields.StartDate),
                EndDt: formfields.EndDate == "" ? null : vm.$options.filters.formatDate(formfields.EndDate),
                Status: formfields.Status,
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveCrewResource(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else {
                        if (vm.CrewResId.CREW_RES_ID > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Resource")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Resource")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchResources();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>