<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_CREW" :LegendArray="[]"
        :RenderRowActionMethod="renderActions" :widgets="[]" IdentityColumn="CREW_RES_ID" :HeaderText="$t('Resource')"
            :listType="['List']" :ListData="ResourceList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords"  v-on:ActionButtonClick="actionButtonClick"  :ListDataCallBackFunction="FetchResources"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'ISLEADER'">
                    <div class="form-group text-center mb-0">
                        <label class="switch disabled">
                            <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                v-model="data.row.ISLEADER" v-bind:name="'IsLogin'" v-disabled="disabled">
                            <span class="slider round" style="cursor:default;">
                                <span class="slider-yes">Yes</span>
                                <span class="slider-no">No</span>
                            </span>
                        </label>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="active">
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Active'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Inactive'">
                            {{ $t('Inactive') }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STARTDT'">
                    <span>
                        {{ $options.filters.formatDate(data.row.STARTDT) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ENDDT'">
                    <span>
                        {{ $options.filters.formatDate(data.row.ENDDT) }}
                    </span>
                </template>
            </template>
        </tg-list>
        <AddEditCrewResourcePopup v-if="AddEditCrewResourcePopup" :CrewResId="CrewResId"></AddEditCrewResourcePopup>
    </section>
</template>
<script>
import Schema from "@/views/crew/CrewResourceSchema";
import DataServices from "@/services/DataService";
import AddEditCrewResourcePopup from '../../components/Modal/AddEditCrewResourcePopup.vue'
export default {
    components: { AddEditCrewResourcePopup },
    data() {
        return {
            AddEditCrewResourcePopup:false,
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            listActions: Schema.listActions,
            GroupStart: 1,
            GroupEnd: 3,
            Headers: [{
                "COLUMN_NAME": "RESOURCE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Resource Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "STARTDT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Start Date",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ENDDT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "End Date",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ISLEADER",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Is Leader",
                "DISPLAY_ORDER": 4,
                settings: {
                    isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }
            },
            ],
            listheaderbuttons:  Schema.buttons,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            search: '',
            GroupStart: 1,
            GroupEnd: 3,
            leftSearchFields: [],
            ResourceList: [],
            CrewResId:0,
            user_id: null,
            companyId: null
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchResources();
    },
    methods: {
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    this.openPopupForAddCrewResource(item)
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchResources: function () {
            var vueObj = this;
            var url = `crewid=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            DataServices.FsmGetResourcesByCrewId(url).then(
                function (response) {
                    vueObj.ResourceList = [];
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'STARTDT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'ENDDT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.ResourceList = response.data.result.data;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;

                            if (vueObj.ResourceList.length > 0) {
                                vueObj.listheaderbuttons[1].isdisabled=true;
                                vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                                vueObj.NoRecord = false;
                            }
                            else {
                                vueObj.NoRecord = true;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                        }                     
                        setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                    }
                });
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else {
                this.search = '';
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchResources();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchResources();
        },

        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchResources();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "CommonAddData":
                    this.openPopupForAddCrewResource(null);
                    break;
                case "BackToList":
                    this.BackToCrewList();
                    break;
            }
        },
        openPopupForAddCrewResource: function (id) {
            if (id == undefined)
                id = 0;
            this.CrewResId = id;
            this.AddEditCrewResourcePopup = !this.AddEditCrewResourcePopup;
        },
        BackToCrewList: function(){
            this.$router.push({ name: 'CrewListing' });
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_CREW_RESOURCE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchResources();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Resource')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Resource')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);
                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
    },
}
</script>