<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">
            <loader :is-visible="isLoading"></loader>
            <div class="left-menu-tab div-click-event hr_jobpost_a">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition" v-for="item in CrewList">
                        <span class="p-name text-white textelipsis">{{ item.CREW_NAME }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 1 }"
                            data-bs-target="#tab-detail-content" href="javascript:void(0)" data-bs-toggle="tab" role="tab"
                            id="tab-Details" v-on:click="loadData('detail');">
                            <em class="fa fa-ticket" aria-hidden="true" aria-selected="true"></em>
                            {{ $t('Details') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 2 }"
                            data-bs-target="#tab-resource-content" href="javascript:void(0)" id="tab-resource-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Resource');">
                            <em class="fa fa-calendar" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Resource') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 3 }"
                            data-bs-target="#tab-service-content" href="javascript:void(0)" id="tab-service-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Service');">
                            <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('ServiceAppointment') }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0 mb-2" v-if="isDetail || isService">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-name text-white" id="span_ForSelectedTabName">{{ tabNameTitle }}</span>
                        <input type="hidden" id="hdnActionName" v-bind:value="Acctab" />
                        <span class="p-actions float-right">
                            <RouterLink to="/Ticketing/Crew" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em>
                                <br />
                                {{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns">
                                <em class="fa fa-angle-up text-white"></em>
                            </a>
                        </span>
                    </div>
                </div>
                <div id="crewData" class="col-md-12 p-0 mt-2" v-if="isDetail">
                    <div class="card mb-3 probx">
                        <div class="card-header">
                            <h2>{{ $t('Details') }}</h2>
                        </div>
                        <div class="card-body">
                            <div class="row" v-for="item in CrewList">
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group"><label><b>{{ $t('CrewName') }}:</b> {{ item.CREW_NAME }}</label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group"><label><b>{{ $t('CrewSize') }}:</b> {{ item.FIRST_NAME }}
                                            {{ item.CREW_SIZE }}</label></div>
                                </div>
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group">
                                        <label>
                                            <b>{{ $t('Status') }}:</b> <span v-if="item.STATUS_ID == 1002">{{ $t('Inactive')
                                            }}</span>
                                            <span v-if="item.STATUS_ID == 1001">{{ $t('Active') }}</span>
                                        </label>
                                    </div>
                                </div>
                                <div class="col-md-6 col-lg-3">
                                    <div class="form-group"><label><b>{{ $t('Description') }}:</b> {{ item.DESCRIPTION
                                    }}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 2 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isResource">
                    <ResourceDatamodal :ResourceTypeId="ticketSourceId"></ResourceDatamodal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 3 }"
                    id="tab-service-content" role="tabpanel" aria-labelledby="tab-service-content" v-if="isService">
                    <ServiceAppointmentDataModal :ResourceTypeId="ticketSourceId"></ServiceAppointmentDataModal>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
import ServiceAppointmentDataModal from '../../views/crew/ServiceAppointmentData.vue'
import ResourceDatamodal from '../../views/crew/ResourceData.vue'
import DataServices from "@/services/DataService";
export default {
    components: { ResourceDatamodal, ServiceAppointmentDataModal },
    data() {
        return {
            CrewList: [],
            isDetail: false,
            isResource: false,
            isService: false,
            NoRecord: false,
            ResourceDatamodal: false,
            ServiceAppointmentDataModal: false,
            tabNameTitle:'',
        }
    },
    created: function () {
        this.loadData("detail");
        this.FetchData();

    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            var url = `crewid=${this.$route.params.id}`;
            DataServices.FsmGetCrewById(url).then(
                function (response) {
                    vueObj.CrewList = [];
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            vueObj.CrewList = response.data.result.data;
                            vueObj.crewName = vueObj.CrewList[0].CREW_NAME;

                            if (vueObj.CrewList.length > 0) {
                                vueObj.NoRecord = false;
                            }
                            else {
                                vueObj.NoRecord = true;
                            }
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                });
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            if (tabName == "detail") {
                vm.isService = false;
                vm.isResource = false;
                vm.isDetail = true;
                vm.tabNameTitle= 'Service Crew Overview';
                vm.activeKey = 1;
            } else if (tabName == "Resource") {
                vm.isDetail = false;
                vm.isService = false;
                vm.isResource = true;
                vm.tabNameTitle='Resource';
                vm.activeKey = 2;
                vm.openPopupForCrewResourceDetail(this.$route.params.id);
            } else if (tabName == "Service") {
                vm.isDetail = false;
                vm.isResource = false;
                vm.isService = true;
                vm.tabNameTitle='Service Appointment';
                vm.activeKey = 3;
                vm.openPopupForCrewServiceDetail(this.$route.params.id);
            }
        },
        openPopupForCrewResourceDetail: function (id) {
            this.ResourceDatamodal = !this.ResourceDatamodal;
        },
        openPopupForCrewServiceDetail: function (id) {
            this.ServiceAppointmentDataModal = !this.ServiceAppointmentDataModal;
        },
    },
}
</script>