<template>
    <section>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="SERVICE_APPOINTMENT" :LegendArray="[]"
            :RenderRowActionMethod="() => { }" :widgets="[]" IdentityColumn="SA_ID" :HeaderText="$t('ServiceAppointment')"
            :listType="['List']" :ListData="SAData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords"
            :ListDataCallBackFunction="FetchServiceAppointment" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'View'">
                    <td class="text-center single-action tourguide_a">
                        <RouterLink :to="'/ServiceAppointment/ViewSADetails/'+data.row.SA_ID" 
                        style="color: dodgerblue" :typetitle="$t('View')" v-bind:dataid="data.row.SA_ID"
                        :title="$t('View')"><em class="fa fa-eye text-info action-icon"></em>
                    </RouterLink>
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'SA_Scheduled_StartTime'">
                    <span>
                        {{ $options.filters.formatDate(data.row.SA_Scheduled_StartTime) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'SA_Scheduled_EndTime'">
                    <span>
                        {{ $options.filters.formatDate(data.row.SA_Scheduled_EndTime) }}
                    </span>
                </template>
            </template>
        </tg-list>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            Headers: [{
                "COLUMN_NAME": "SA_Number",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "SA Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "Subject",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "crew_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Crew Name",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "SA_Scheduled_StartTime",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scheduled Start Time",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "SA_Scheduled_EndTime",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Scheduled End Time",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "View",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Action",
                "DISPLAY_ORDER": 6
            },
            ],
            listheaderbuttons: null,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            leftSearchFields: null,
            SAData: []
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchServiceAppointment();
    },
    methods: {
        FetchServiceAppointment: function () {
            var vueObj = this;
            var url = `crewid=${this.$route.params.id}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            DataServices.FsmGetServiceAppointmentByCrew(url).then(
                function (response) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'SA_Scheduled_StartTime') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'SA_Scheduled_EndTime') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'View') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    vueObj.SAData = [];
                    if (response.data != "" && response.data != null) {
                        if (response.data.result.data[0].TOTALRECORDS > 0) {
                            vueObj.SAData = response.data.result.data;

                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;

                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.SAData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.SAData = [];
                        vueObj.Headers = [];
                        vueObj.TotalRecords = 0
                    }                   
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },

        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchServiceAppointment();
        },

        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchServiceAppointment();
        }
    },
}
</script>